* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    font-family: sans-serif;
}

html,
body {
    height: 100%;
    width: 100%;
    overflow-x: hidden;

}

img {
    object-fit: fill;
}

hr {
    width: 1px;
    height: auto;
    color: #707070;
    background-color: #707070;
    opacity: .2;
}

/* For Custom scroll bar start */
body::-webkit-scrollbar {
    width: 1em;
}

body::-webkit-scrollbar-track {
    background: #000000;
}

body::-webkit-scrollbar-thumb {
    background: linear-gradient(#51446F, #000000);
    border: .20em solid #51446F;
    border-radius: 100vw;
    transition: .1s ease;
    margin-block: .5em;
}

body::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(#000000, #51446F);
}

/* For Custom scroll bar end  */

/* ********************************************************************* CONTAINER START *********************************************************************  */

.upper_container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #333333;
}

.container_section {
    max-width: 100%;
    display: flex;
    position: relative;
}

.left_part_of_container {
    width: 300px;
    display: flex;
    justify-content: center;
    overflow: hidden;
}

.middle_part_of_container {
    width: 850px;
    overflow: hidden;
}

.right_part_of_container {
    width: 415px;
    overflow: hidden;
}

/* ********************************************************************* CONTAINER END ********************************************************************* */


/* ********************************************************************* SIDEBAR SECTION START *********************************************************************  */
.sidebar {
    width: 300px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: #333333;

}

.sidebar .top_sidebar_logo,
.sidebar .top_sidebar_logo img {
    width: 90px;
    height: 90px;
    border-radius: 50px;
}

.sidebar .middle_sidebar_item {
    display: flex;
    flex-direction: column;
    gap: 10px;
    text-align: center;
    list-style: none;
}

.sidebar .middle_sidebar_item .links {
    cursor: pointer;
    width: 200px;
    padding: 10px;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    color: #FFFFFF;
    font-size: 20px;
    background-color: #424242;
    text-decoration: none;
    border: 1px solid #707070;
}


.active-styled {
    background-color: #FFFFFF;
    color: #333333;
    cursor: pointer;
    width: 200px;
    padding: 10px;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    font-size: 20px;
    text-decoration: none;
    border: 1px solid #FFFFFF;
}

.sidebar .bottom_sidebar_menu {
    width: 200px;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    color: #FFFFFF;
    font-size: 20px;
    background-color: #424242;
    text-decoration: none;
    border: 1px solid #707070;
}

.sidebar .bottom_sidebar_menu h1 {
    color: #FFFFFF;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sidebar .bottom_sidebar_menu .bar {
    cursor: pointer;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media only screen and (max-width:1440px) {
    .right_part_of_container {
        display: none;
    }

}


@media only screen and (max-width:1024px) {

    .left_part_of_container {
        background-color: aqua;
        position: absolute;
        left: -90px;
        top: 0;
        z-index: 20;
    }

    .sidebar {
        width: 100%;
    }

    .sidebar .middle_sidebar_item .links {
        width: 150px;
        border-radius: 0;
        font-size: 15px;

    }

    .active-styled {
        width: 150px;
        border-radius: 0px;
        font-size: 15px;
    }

    .sidebar .bottom_sidebar_menu {
        width: 150px;
        border-radius: 0;
        font-size: 20px;
    }

    .sidebar .bottom_sidebar_menu h1 {
        font-size: 15px;
    }

}

