    background: #FFFFFF;
    border: 1px solid #707070;
    border-radius: 90px;
    opacity: 1;
    text-align: center;
    font-size: 20px;
    letter-spacing: 0px;
    color: #000000;
    opacity: 1;
    padding: 5px 15px;
}

