        font-size: 15px;
    }
}

@media only screen and (max-width:760px) {
    .navbar .logo {
        padding: 10px;
    }

    .hamburgur {
        display: block;
        color: white;
        font-size: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .navbar {
        position: relative;
        left: 0;
        top: 0;
        width: 100%;
    }

    .navbar .nav_list {
        display: none;
        width: 100%;
        position: fixed;
        left: 0;
        transition: all .5 ease-in;
    }

    .nav_list nav {
        width: 100%;
        height: auto;
        position: fixed;
        top: 50px;
        left: 0px;
        background-color: #51446F;
        transition: all .5 ease-in;
    }

    .nav_list nav ul {
        width: 100%;
        display: flex;
        flex-direction: column;
    }

    .nav_list nav ul button {
        float: left;
    }

    .nav_list nav ul li {
        width: 100%;
        border-color: #000000;
        border-width: 0px 0px 2px 0px;
        border-style: solid;
    }

    .navbar .nav_list_block {
        display: block;
    }
}

